import React from 'react';
import { Link } from 'react-router-dom';
import './AgriVek.css';

const Navbar = () => {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container">
        <Link className="navbar-title" to="/">AgriVek</Link>

        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" to="/" style={navLinkStyle}>Home</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/vehicle-card" style={navLinkStyle}>Vehicle Card</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/rent" style={navLinkStyle}>Rent</Link>
            </li>
            
          </ul>
        </div>
      </div>
    </nav>
  );
};

const navLinkStyle = {
  fontSize: "20px",
  color: '#34cceb',
  fontFamily: "'Courier New', Courier, monospace",
  fontWeight: "600",
  marginLeft: "10px"
};

export default Navbar;
