import React, { useState, useEffect } from 'react';
import { Card, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import './AgriVek.css';

const vehicles = [
  {
    name: "Tractor",
    image: "https://as1.ftcdn.net/v2/jpg/02/78/21/80/1000_F_278218000_M8XeJ17iOrQb35aOgfBsaJZxZHE03nUG.jpg",
    description: "A tractor is a versatile vehicle used in agriculture for various tasks such as plowing, planting, and hauling.",
  },
  {
    name: "Combine Harvester",
    image: "https://img.freepik.com/free-photo/tractor-working-field_342744-535.jpg?t=st=1709998380~exp=1710001980~hmac=cab1dc6a75a9e48b451f104b27ed163891e3a725fce39229712c3d31f0369934&w=996",
    description: "A combine harvester, also known as a combine, is a machine used to harvest grains such as wheat, barley, and corn.",
  },
  {
    name: "Plough",
    image: "https://media.istockphoto.com/id/1301286137/photo/a-plow-for-cultivating-the-soil-is-attached-to-a-dusty-tractor-equipment-in-industry-of.jpg?s=2048x2048&w=is&k=20&c=SduxcEsNn1oG_GqoQeyC3PXbv1zjoy9yaI32fXdIFpc=",
    description: "A plough is a farming implement used for turning over the soil to prepare it for planting.",
  },
  {
    name: "Seed Drill",
    image: "https://5.imimg.com/data5/SELLER/Default/2020/8/ML/CN/ZC/82413696/agricultural-seed-drill-1000x1000.jpg",
    description: "A seed drill is a device used in agriculture for planting seeds precisely in rows at specific depths.",
  },
  {
    name: "Sprayer",
    image: "https://5.imimg.com/data5/SELLER/Default/2022/6/TB/BI/YN/3164304/30hp-vehicle-mounted-sprayer-500x500.jpg",
    description: "A sprayer is a piece of equipment used to apply pesticides, herbicides, and fertilizers to crops.",
  },
  {
    name: "Harrow",
    image: "https://www.fieldking.com/images/tillage/harrow/lg/heavy-duty-hydraulic-harrow.png",
    description: "A harrow is an implement used to break up and smooth out the surface of the soil after plowing.",
  },
  {
    name: "Cultivator",
    image: "https://5.imimg.com/data5/SELLER/Default/2021/1/CQ/ZS/KY/56733367/cultivator-channel-body-500x500.jpg",
    description: "A cultivator is a farm implement used to stir and pulverize the soil before planting.",
  },
  {
    name: "Planter",
    image: "https://e7.pngegg.com/pngimages/367/343/png-clipart-planter-tractor-machine-stara-agriculture-tractor-agriculture-vehicle.png",
    description: "A planter is a machine used for planting seeds or seedlings into the soil at precise intervals.",
  },
  {
    name: "Thresher",
    image: "https://img.freepik.com/premium-photo/unloading-grain-from-combine-into-truck_120587-16337.jpg?w=996",
    description: "A thresher is a farm machine used to separate grains from their husks and stalks.",
  },
  {
    name: "Rotary Tiller",
    image: "https://e7.pngegg.com/pngimages/619/869/png-clipart-shaktimaan-cultivator-tractor-agriculture-manufacturing-cultivation-agriculture-vehicle.png",
    description: "A rotary tiller is a motorized cultivator with rotating blades used to till soil in gardens and farms.",
  }
];

const ExploreVehiclesPage = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    if (storedUsername) {
      setIsLoggedIn(true);
    }
  }, []);

  return (
    <div className="Explore-Vehicles-container">
      <h2 className='Explore-heading'>Agriculture Vehicles List</h2>
      {alertMessage && <Alert variant="danger">{alertMessage}</Alert>}
      <div className="row">
        {vehicles.map((vehicle, index) => (
          <div key={index} className="col-lg-3 col-md-4 mb-4">
            <Card style={{ width: '100%', backgroundColor: 'rgba(255, 255, 255, 0.7)', borderRadius: '10px', boxShadow: '0 4px 8px rgba(0,0,0,0.1)', border: '2px solid rgb(52, 47, 47)' }}>
              <Card.Img variant="top" src={vehicle.image} style={{ height: '200px', objectFit: 'cover', borderRadius: '10px 10px 0 0' }} />
              <Card.Body style={{ padding: '10px' }}>
                <Card.Title style={{ fontSize: '18px', fontWeight: 'bolder', marginBottom: '10px', color: 'rgb(52, 47, 47)', fontFamily: 'Courier New, Courier, monospace' }}>{vehicle.name}</Card.Title>
                <Card.Text style={{ fontSize: '16px', color: 'rgb(52, 47, 47)', fontWeight: 500, fontFamily: 'Courier New, Courier, monospace' }}>{vehicle.description}</Card.Text>
              </Card.Body>
            </Card>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ExploreVehiclesPage;
