import React, { useState, useEffect } from "react";
import { Modal, Button } from "react-bootstrap";

const VehicleCard = () => {
    const [vehicles, setVehicles] = useState([]);
    const [selectedVehicle, setSelectedVehicle] = useState(null);
    const [isHovered, setIsHovered] = useState(null);
    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        fetch("http://localhost:8081/Vehicles")
            .then((response) => response.json())
            .then((data) => setVehicles(data))
            .catch((error) => console.error("Error fetching vehicles:", error));
    }, []);

    const handleShow = (vehicle) => {
        setSelectedVehicle(vehicle);
        setShowModal(true);
    };

    const handleClose = () => setShowModal(false);

    return (
        <div className="container">
            <div className="row">
                {vehicles.map((vehicle) => (
                    <div key={vehicle.vehicleId} className="col-md-4 mb-3">
                        <div
                            className="card"
                            style={{
                                border: "2px solid lightgreen",
                                borderRadius: "12px",
                                boxShadow: "5px 5px 15px rgba(0, 0, 0, 0.1)",
                                marginBottom: "20px",
                                height: "100%",
                                transition: "all 0.2s ease-in-out",
                                transform: isHovered === vehicle.vehicleId ? "scale(1.02)" : "scale(1)",
                                cursor: "pointer",
                            }}
                            onMouseEnter={() => setIsHovered(vehicle.vehicleId)}
                            onMouseLeave={() => setIsHovered(null)}
                            onClick={() => handleShow(vehicle)}
                        >
                            <div className="card-body" style={{ padding: "20px", height: "100%", display: "flex", flexDirection: "column", justifyContent: "space-between" }}>
                                <h5 className="card-title" style={{ fontSize: "1.5rem", marginBottom: "10px", fontFamily: "Permanent Marker", color: "green" }}>
                                    {vehicle.vehicle_name || "N/A"}
                                </h5>
                                <img src={vehicle.imageURL} alt={`Vehicle ${vehicle.vehicleName}`} style={{ width: "90%", height: "auto", marginBottom: "10px" }} />
                                <div className="card-info">
                                    <p style={{ marginBottom: "8px" }}>
                                        <strong>Category:</strong> {vehicle.category || "N/A"}
                                    </p>
                                    <p style={{ marginBottom: "8px" }}>
                                        <strong>Price:</strong> {vehicle.price || "N/A"}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            {selectedVehicle && (
                <Modal show={showModal} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>{selectedVehicle.vehicle_name || "N/A"}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <img src={selectedVehicle.imageURL} alt={`Vehicle ${selectedVehicle.vehicleName}`} style={{ width: "100%", marginBottom: "20px" }} />
                        <p><strong>Category:</strong> {selectedVehicle.category || "N/A"}</p>
                        <p><strong>Price:</strong> {selectedVehicle.price || "N/A"}</p>
                        <p><strong>Uses:</strong> {selectedVehicle.uses || "N/A"}</p>
                        <p><strong>Purpose:</strong> {selectedVehicle.purpose || "N/A"}</p>
                        <p><strong>Description:</strong> {selectedVehicle.description || "N/A"}</p>
                        <p><strong>Type of Crop:</strong> {selectedVehicle.type_of_crop || "N/A"}</p>
                        {selectedVehicle.videoURL && (
                            <p><strong>Video:</strong> <a href={selectedVehicle.videoURL} target="_blank" rel="noopener noreferrer">Watch Video</a></p>
                        )}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            )}
        </div>
    );
};

export default VehicleCard;
