import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Form, Button, Modal, FormControl } from 'react-bootstrap';
import './AgriVek.css';

const Vehicles = () => {
  const [vehicles, setVehicles] = useState([]);
  const [rentForms, setRentForms] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [formData, setFormData] = useState(null);
  const [availability, setAvailability] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [username, setUsername] = useState('');

  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    if (storedUsername) {
      setIsLoggedIn(true);
      setUsername(storedUsername);
    } else {
      setIsLoggedIn(false);
      setUsername('');
    }
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://localhost:8081/rent');
        if (!response.ok) throw new Error('Failed to fetch vehicles');

        const data = await response.json();
        setVehicles(data);
        setRentForms(Array(data.length).fill(false));
      } catch (error) {
        setError('Error fetching vehicles. Try again later.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const handleRentNow = (index) => {
    setRentForms((prevForms) => prevForms.map((value, i) => (i === index ? !value : false)));
  };

  const handleRentSubmit = (e, vehicleId, index) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const rentData = Object.fromEntries(formData.entries());

    if (!/^\d{10}$/.test(rentData.contact)) {
      alert('Contact number must be exactly 10 digits');
      return;
    }

    setFormData({ ...rentData, vehicleId, index });
    setShowModal(true);
  };

  const confirmRent = () => {
    const { index, days } = formData;
    const rentedDays = parseInt(days, 10);

    const today = new Date();
    today.setDate(today.getDate() + rentedDays);
    const availableDate = today.toDateString();

    setAvailability((prev) => ({
      ...prev,
      [index]: `Available after ${rentedDays} days (${availableDate})`,
    }));

    setShowModal(false);
    setRentForms((prevForms) => prevForms.map((value, i) => (i === index ? false : value)));
  };

  return (
    <div className="container mt-3">
      {isLoggedIn && <h3 className="welcomeMsg text-center mb-4">Welcome, {username}!</h3>}

      {error && <p style={{ color: 'red', textAlign: 'center' }}>{error}</p>}
      {loading ? <p>Loading vehicles...</p> : (
        <Row xs={1} md={2} lg={3} className="g-4">
          {vehicles.map((vehicle, index) => (
            <Col key={index}>
              <Card style={{ border: '2px solid black', marginBottom: "15px" }}>
                <Card.Img
                  variant="top"
                  src={vehicle.uploadedImg || 'fallback-image.jpg'}
                  onError={(e) => { e.target.src = 'fallback-image.jpg'; }}
                  style={{ maxWidth: '100%', maxHeight: '280px' }}
                />
                <Card.Body>
                  <Card.Title><strong>{vehicle.vehicleName}</strong></Card.Title>
                  <Card.Text>
                    <span>Registration:</span> {vehicle.registrationNum} <br />
                    <span>Price Per Day:</span> &#8377;{vehicle.pricePerDay} <br />
                    <span>Location:</span> {vehicle.location} <br />
                    <span>Description:</span> {vehicle.description} <br />
                    <span>Owner:</span> {vehicle.ownerName} <br />
                    <span>Contact:</span> {vehicle.contactDetails} <br />
                    <span style={{ fontWeight: 'bold', color: 'red' }}>
                      {availability[index] || 'Available Now'}
                    </span>
                  </Card.Text>
                  {!availability[index] && ( 
                    <Button variant="primary" onClick={() => handleRentNow(index)}>Rent Now</Button>
                  )}
                  {rentForms[index] && (
                    <Form onSubmit={(e) => handleRentSubmit(e, vehicle._id, index)} className="mt-3">
                      <Form.Group>
                        <Form.Label>Name</Form.Label>
                        <FormControl type="text" name="name" required />
                      </Form.Group>
                      <Form.Group>
                        <Form.Label>Contact No</Form.Label>
                        <FormControl type="text" name="contact" maxLength="10" pattern="\d{10}" required />
                      </Form.Group>
                      <Form.Group>
                        <Form.Label>Days</Form.Label>
                        <FormControl type="number" name="days" min="1" required />
                      </Form.Group>
                      <Button variant="primary" type="submit">Rent</Button>
                    </Form>
                  )}
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      )}

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Confirm Rent</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure you want to rent this vehicle?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>Cancel</Button>
          <Button variant="primary" onClick={confirmRent}>Confirm</Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Vehicles;
